<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="transactionTypeTableData" :noDataText="transactionTypeTableNoDataText" :sst="true" search @search="filterTransactionTypeTable" @sort="orderTransactionTypeTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadTransactionTypeTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="transactionTypeTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ transactionTypeTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="transactionTypeTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="typeName">
            {{ this.$t('TYPE_NAME') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].typeName">
              {{ data[index].typeName }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="transactionTypeTableTotalPages" v-model="transactionTypeTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import TransactionTypeTableMixin from '@/mixins/account/TransactionTypeTableMixin'

export default {
  name: 'transaction-type-popup',
  mixins: [PopupMixin, TransactionTypeTableMixin],
  mounted: function () {
    this.loadTransactionTypeTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
