<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card id="loading-container" class="vs-con-loading__container mb-base">
      <div class="flex justify-between">
        <h4 class="mb-4">{{ $t('SEARCH_CRITERIA') }}</h4>
        <vs-switch vs-icon-on="fa-chevron-up" vs-icon-off="fa-chevron-down" icon-pack="far" v-model="showAdvanced"></vs-switch>
      </div>

      <div class="nv-row mb-4">
        <div class="nv-col w-full required">
          <label class="vs-input--label">{{ $t('ACCOUNT') }}</label>
          <account-lookup :title="$t('ACCOUNT')" valueField="accountNumber" displayField="accountNumber" @select-record="selectAccountRecord"/>
        </div>
      </div>

      <div class="nv-row mb-4">
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('FROM_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="fromDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
        <div class="nv-col sm:w-1/2 w-full required">
          <label class="vs-input--label">{{ $t('TO_DATE') }}</label>
          <nv-input-group>
            <template slot="append">
              <div class="append-text bg-dark">
                <i class="far fa-calendar-alt"></i>
              </div>
            </template>
            <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="toDate" :format="dateFormat"></datepicker>
          </nv-input-group>
        </div>
      </div>

      <div v-if="showAdvanced">
        <div class="nv-row mb-4">
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('TRANSACTION_TYPE') }}</label>
            <transaction-type-lookup :title="$t('TRANSACTION_TYPE')" :recordValue="typeId" :recordDisplay="typeName" valueField="typeId" displayField="typeName" @select-record="selectTransactionType"/>
          </div>
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('ORDER_BY') }}</label>
            <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="orderByField">
              <vs-select-item v-for="(orderByField, index) in orderByFields" :key="index" :value="orderByField.enumId" :text="orderByField.description"/>
            </vs-select>
          </div>
        </div>

        <div class="nv-row mb-4">
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('FROM_AMOUNT') }}</label>
            <div class="flex">
              <vs-input-number v-model="fromAmount" min="0"/>
            </div>
          </div>
          <div class="nv-col sm:w-1/2 w-full">
            <label class="vs-input--label">{{ $t('TO_AMOUNT') }}</label>
            <div class="flex">
              <vs-input-number v-model="toAmount" min="0"/>
            </div>
          </div>
        </div>
      </div>

      <div class="nv-row">
        <div class="nv-col w-full">
          <div class="flex justify-between">
            <vs-dropdown vs-trigger-click>
              <a class="flex items-center" href="#">
                {{ $t('CHOOSE_INTERVAL') }}
                <i class="far fa-chevron-down ml-2"></i>
              </a>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="chooseInterval('day')">
                  {{ $t('PAST_DAY') }}
                </vs-dropdown-item>
                <vs-dropdown-item @click="chooseInterval('week')">
                  {{ $t('PAST_WEEK') }}
                </vs-dropdown-item>
                <vs-dropdown-item @click="chooseInterval('month')">
                  {{ $t('PAST_MONTH') }}
                </vs-dropdown-item>
                <vs-dropdown-item @click="chooseInterval('year')">
                  {{ $t('PAST_YEAR') }}
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
            <vs-button class="py-3" @click="showTransactions" :disabled="transactionTableBusy">{{ $t('SUBMIT') }}</vs-button>
          </div>
        </div>
      </div>
    </nv-card>

    <nv-card v-show="transactionTableVisible && !transactionTableBusy">
      <h4 class="mb-base">{{ $t('TRANSACTIONS_BETWEEN') }} {{ fromDate | formatUnixDate }} &amp; {{ toDate | formatUnixDate }}</h4>
      <vs-table :data="transactionTableData" :noDataText="transactionTableNoDataText" search>
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="exportXls(transactionTableData, columnDefs)" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
            <vs-button class="p-3 mr-4" @click="exportCsv(transactionTableData, columnDefs)" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>

            <vs-button type="filled" icon="fa-file-pdf" icon-pack="fal" :disabled="transactionTableBusy" class="p-3 mr-4" :href="exportUrl">{{ $t('EXPORT_PDF') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ transactionTablePagingInfo }}</span>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="postingDate">
            {{ this.$t('POSTING_DATE') }}
          </vs-th>
          <vs-th sort-key="valueDate">
            {{ this.$t('VALUE_DATE') }}
          </vs-th>
          <vs-th sort-key="narrative">
            {{ this.$t('TRANSACTION_DESCRIPTION') }}
          </vs-th>
          <vs-th sort-key="transactionId">
            {{ this.$t('TRANSACTION_ID') }}
          </vs-th>
          <vs-th sort-key="amount">
            {{ this.$t('DEBIT_AMOUNT') }}
          </vs-th>
          <vs-th sort-key="amount">
            {{ this.$t('CREDIT_AMOUNT') }}
          </vs-th>
          <vs-th sort-key="accountBalance">
            {{ this.$t('BALANCE') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].postingDate">
              {{ data[index].postingDate | formatUnixDate | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].valueDate">
              {{ data[index].valueDate | formatUnixDate | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].narrative">
              {{ data[index].narrative | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].transactionId">
              <router-link :to="{ name: 'ViewAccountTransaction', params: { id: data[index].transactionId, accountNumber: accountNumber, fromDate: fromDate, toDate: toDate } }">{{ data[index].transactionId | placeholder('-') }}</router-link>
            </vs-td>
            <vs-td :data="data[index].amount">
              <span :class="positiveNumberStyle(data[index].amount)">{{ data[index].drCrIndicator === 'D' ? data[index].amount : '' | currency('') | placeholder('-') }}</span>
            </vs-td>
            <vs-td :data="data[index].amount">
              <span :class="negativeNumberStyle(data[index].amount)">{{ data[index].drCrIndicator === 'C' ? data[index].amount : '' | currency('') | placeholder('-') }}</span>
            </vs-td>
            <vs-td :data="data[index].accountBalance">
              <span :class="negativeNumberStyle(data[index].accountBalance)">{{ data[index].accountBalance | currency('') | placeholder('-') }}</span>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import AccountTransactionTableMixin from '@/mixins/account/AccountTransactionTableMixin'
import AccountLookup from '@/views/account/account-root/AccountLookup'
import TransactionTypeLookup from '@/views/account/transaction-type/TransactionTypeLookup'
import Datepicker from 'vuejs-datepicker'
import EnumerationService from '@/services/commons/EnumerationService'

export default {
  name: 'account-transaction',
  mixins: [AccountTransactionTableMixin, AlertMixin, NumberStyleMixin],
  components: {
    AccountLookup,
    TransactionTypeLookup,
    Datepicker
  },
  data: function () {
    return {
      showAdvanced: false,
      accountNumber: '',
      typeId: '',
      typeName: '',
      orderByFields: [],
      orderByField: 'TRX_ORDER_FLD_POST_DATE',
      fromDate: null,
      toDate: new Date(),
      fromAmount: 0,
      toAmount: 0,
      transactionTableVisible: false
    }
  },
  computed: {
    exportUrl: function (item) {
      let baseUrl = process.env.NODE_ENV === 'development' ? 'http://localhost:8080' : ''
      let appId = this.$store.getters.appId
      let customerId = this.$store.getters.activeExternalId.externalId
      return `${baseUrl}/apps/Connect/DownloadSecure/downloadAccountTransactions?sourceAppId=${appId}&customerId=${customerId}&accountNumber=${this.accountNumber}&fromDate=${this.getTime(this.fromDate)}&toDate=${this.getTime(this.toDate)}&fromAmount=${this.fromAmount}&toAmount=${this.toAmount}&typeId=${this.typeId}&orderByField=${this.orderByField}`
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'TransactionOrderField',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.orderByFields = response.data.enumerationList || []
      })
    },
    showTransactions () {
      if (this.accountNumber && this.fromDate) {
        this.transactionTableVisible = true
        this.loadTransactionTableData()
      }
    },
    selectAccountRecord (record) {
      this.accountNumber = record.accountNumber
    },
    selectTransactionType (record) {
      this.typeId = record.typeId
      this.typeName = record.typeName
    }
  },
  watch: {
    accountNumber: function () {
      this.transactionTableVisible = false
      this.transactionTableData = []
      this.transactionTableTotalRows = 0
    },
    fromDate: function () {
      this.transactionTableVisible = false
      this.transactionTableData = []
      this.transactionTableTotalRows = 0
    },
    toDate: function () {
      this.transactionTableVisible = false
      this.transactionTableData = []
      this.transactionTableTotalRows = 0
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
