import AccountService from '@/services/account/AccountService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TransactionTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultTransactionTypeTableFilter: {
      type: String,
      default: null
    },
    defaultTransactionTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      transactionTypeTableData: [],
      transactionTypeTableFilterTimer: null,
      transactionTypeTableFilter: this.defaultTransactionTypeTableFilter,
      transactionTypeTableOrderBy: this.defaultTransactionTypeTableOrderBy,
      transactionTypeTableContainer: 'loading-container',
      transactionTypeTableBusy: false,
      transactionTypeTableTotalRows: 0,
      transactionTypeTableCurrentPage: 1,
      transactionTypeTablePerPage: 10
    }
  },
  computed: {
    transactionTypeTableNoDataText: function () {
      return this.transactionTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    transactionTypeTableTotalPages: function () {
      return Math.ceil(this.transactionTypeTableTotalRows / this.transactionTypeTablePerPage)
    },
    transactionTypeTablePagingInfo: function () {
      return this.pagingInfo(this.transactionTypeTableCurrentPage, this.transactionTypeTablePerPage, this.transactionTypeTableTotalRows)
    }
  },
  methods: {
    loadTransactionTypeTableData (loadingContainerId) {
      let self = this
      this.transactionTypeTableBusy = true
      this.transactionTypeTableContainer = loadingContainerId || this.transactionTypeTableContainer
      this.startLoading(this.transactionTypeTableContainer)
      AccountService.transactionTypes({
        pageIndex: this.transactionTypeTableCurrentPage - 1,
        pageSize: this.transactionTypeTablePerPage,
        filter: this.transactionTypeTableFilter,
        orderByField: this.transactionTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.transactionTypeTableContainer)
        self.transactionTypeTableBusy = false
        self.transactionTypeTableTotalRows = response.data.totalRows
        self.transactionTypeTableData = response.data.transactionTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.transactionTypeTableContainer)
        self.transactionTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTransactionTypeTable () {
      this.loadTransactionTypeTableData()
    },
    filterTransactionTypeTable (filter) {
      let self = this
      if (this.transactionTypeTableFilter !== filter) {
        clearTimeout(this.transactionTypeTableFilterTimer)
        this.transactionTypeTableFilterTimer = setTimeout(() => {
          self.transactionTypeTableFilter = filter
          self.loadTransactionTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.transactionTypeTableFilterTimer)
      }
    },
    orderTransactionTypeTable (orderBy, active) {
      this.transactionTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTransactionTypeTableData()
    }
  },
  watch: {
    transactionTypeTableCurrentPage: function () {
      this.refreshTransactionTypeTable()
    },
    transactionTypeTablePerPage: function () {
      this.refreshTransactionTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshTransactionTypeTable()
    }
  }
}

export default TransactionTypeTableMixin
